a:hover {
    text-decoration: underline;
  }

table{
  width:80%;
  margin-left: auto; margin-right: auto;
}

th{
  border-bottom:1px solid gray;
}

td{
  text-align: center;
  border-bottom:1px solid gray;
}

#link{
  color:#428bca;
}

button{
  color:white;
  background-color: #2d6ca2;
  border-radius: 3px;
  padding: 5px 10px;

}

input[type="submit"]{
  color:white;
  background-color: #2d6ca2;
  border-radius: 3px;
  padding: 5px 10px;
}

#add_write_button{
  position: relative;

}

.pagination{
  font-size: 15px;
  color: #428bca;
  border: 1px solid #ddd;
  padding: 5px 10px;
}

#pagination_span{
  display: flex;
  justify-content: center;
  position: relative;
  top:30px;
}

form{
  text-align: center;
}

form *{
  width:50%;
  align-self: center;
}

#button_item_3 *{
  margin-right: 10px;

}

#button_item_3{
  text-align: center;
}

#tolist{
  color:white;
  background-color: #2d6ca2;
  border-radius: 3px;
  padding: 5px 10px;
  margin-top: 10px;
  width:50%;
  
}

#write_form_div{
  text-align: center;

}

#add_write_button_outer_div {
  text-align: center;
}

#add_write_button{
  margin-top: 30px;
}
